<script>
  import Recall from "./recall.svelte";
  import View from "./view.svelte";
  import { push } from "svelte-spa-router";

  // {DomElement}
  export let listElement;
  // {DomElement}
  export let playElement;

  /*export let data = [
    "monday",
    "tuesday",
    "wednesday",
    "thursday",
    "friday",
    "saturday",
    "sunday",
    "mandag",
    "tirsdag",
    "onsdag",
    "torsdag",
    "fredag",
    "lrdag",
    "sndag"
  ];
  */
  export let data = [];
  export let gameSize = 7;
  export let speed = 1;

  playElement.style.display = "";
  listElement.style.display = "none";

  function handleClose(event) {
    playElement.style.display = "none";
    listElement.style.display = "";
    push("/");
  }

  let playData = [];
  // This needs to pick the data
  let state = "not-playing";

  const shuffle = arr =>
    arr
      .map(a => [Math.random(), a])
      .sort((a, b) => a[0] - b[0])
      .map(a => a[1]);

  function play() {
    // reduce to 7
    // shuffle
    let temp = shuffle(data);
    playData = temp.slice(0, gameSize);
    state = "playing";
  }

  function finished(event) {
    if (event.detail.playAgain) {
      play();
      return;
    }

    state = "not-playing";
  }

  function handleFinished() {
    state = "recall";
  }

  $: maxSize = data.length;
</script>

