<script>
  import { push } from "svelte-spa-router";
  // {DomElement}
  export let listElement;
  // {DomElement}
  export let playElement;
  // learnalist aList object
  // This is horrible, as it works on aList not alist
  export let aList;

  playElement.style.display = "";
  listElement.style.display = "none";

  let loops = 0;
  let index = -1;
  let firstTime =
    "Welcome, to beginning, click next, or use the right arrow key..";
  let show = firstTime;
  let nextTimeIsLoop = 0;

  function forward(event) {
    index += 1;

    if (!aList.data[index]) {
      index = 0;
      nextTimeIsLoop = 1;
    }

    if (nextTimeIsLoop) {
      loops += 1;
      nextTimeIsLoop = 0;
    }

    show = aList.data[index];
  }

  function backward() {
    index -= 1;
    if (index >= 0) {
      show = aList.data[index];
    } else {
      show = firstTime;
      index = -1;
    }
  }

  function handleClose(event) {
    playElement.style.display = "none";
    listElement.style.display = "";
    push("/");
  }

  function handleKeydown(event) {
    switch (event.code) {
      case "ArrowLeft":
        backward(event);
        break;
      case "Space":
      case "ArrowRight":
        forward(event);
        break;
      default:
        console.log(event);
        console.log(`pressed the ${event.key} key`);
        break;
    }
  }
</script>

