<script>
  import { createEventDispatcher } from "svelte";

  const dispatch = createEventDispatcher();
  export let data = [];

  let state = "playing";
  let playing = false;
  // clean the inputs
  let found = [];
  let playData = [];
  let guesses = [];
  let hasChecked = false;

  playData = data.map(item => {
    return clean(item);
  });

  let leftToFind = playData.length;

  playing = true;
  let perfect = false;

  let feedback = Array(playData.length).fill("");
  let results = [];
  let attempts = 0;
  function check() {
    attempts = attempts + 1;
    hasChecked = true;
    console.log(guesses);
    results = guesses.map(input => {
      return clean(input);
    });
    // Get the unique results
    let uniques = Array.from(new Set(results));

    uniques = uniques.filter(item => playData.includes(item));

    let lookUp = uniques.map(item => {
      return {
        data: item,
        position: -1
      };
    });

    results.forEach((input, position) => {
      lookUp = lookUp.map(item => {
        // skip if already found
        if (item.position !== -1) {
          return item;
        }

        if (item.data !== input) {
          return item;
        }

        item.position = position;
        return item;
      });
    });

    // Set all to not found
    feedback = Array(playData.length).fill("notfound");

    lookUp = lookUp.map(item => {
      if (item.position === -1) {
        return item;
      }

      feedback[item.position] = "found";
      return item;
    });

    leftToFind = playData.length - uniques.length;

    if (leftToFind === 0) {
      state = "finished";
      if (attempts === 1) {
        perfect = JSON.stringify(results) === JSON.stringify(playData);
      }
      console.log("actual", JSON.stringify(playData));
      console.log("guesses", JSON.stringify(results));
    }
  }

  function playAgain() {
    dispatch("finished", {
      perfect: perfect,
      attempts: attempts,
      playAgain: true
    });
  }

  function restart() {
    dispatch("finished", {
      perfect: perfect,
      attempts: attempts,
      playAgain: false
    });
  }

  function showMe() {
    state = "show-me";
  }

  function clean(input) {
    // TODO have the UI allow for more options
    return input.toLowerCase();
  }
</script>

